import React from 'react';
import { Button } from 'react-bootstrap';

export default class InnerComponent extends React.Component {
    constructor(props){
        super(props);
    }
    render(){
        return(
            <div className="container">
                <div className="header">{this.props.data.Problem}</div>
                <div className="desc" style={{height:'100%'}}>
                {
                    this.props.data.Desc.split("\n").map((i,key) => {
                        return <div key={key}>{i}<br /></div>;
                    })
                }
                </div>
                {
                    this.props.data.Solution.map((sol, index) => {
                        return (
                            <div key={index} style={{margin: '1rem'}}>
                            {sol.Header === null ? null : <div style={{fontWeight:'bold'}}><i>{sol.Header}</i></div> }
                            {sol.code === null ? null : 
                            <pre style={{marginLeft:'1rem',marginTop:'.5rem',color:'firebrick'}}>
                                {sol.code}
                            </pre> 
                            }
                            </div>
                        )
                    })
                }
                { this.props.data.Input === null ? null :
                <div style={{margin: '1rem'}}>
                    <span style={{fontWeight:'bold'}}><i>Input: &nbsp;</i></span><span><pre>{this.props.data.Input}</pre></span>
                </div>
                }
                { this.props.data.Output === null ? null :
                <div style={{margin: '1rem'}}>
                    <span style={{fontWeight:'bold'}}><i>Output: &nbsp;</i></span><span><pre>{this.props.data.Output}</pre></span>
                </div>
                }
                <br></br>
                <div style={{textAlign:'center'}}>
                    <Button variant="outline-dark" onClick={this.props.reset} 
                    style={{width:'8rem',fontWeight:'bold'}}>Back</Button>
                </div>
                <br></br>
            </div>
        )
    }
}